.App {
  text-align: center;
  background-color: aliceblue;
}

h2 {
  font-size: 40px;
}

.character-box {
  display: flex;
  flex-wrap: wrap;

}

.character {
  height: 150px;
}



.App-link {
  color: #61dafb;
}

@media screen and (max-width: 1340px) {
  img.profile-pic {
    height: 140px;
  }
}

@media screen and (max-width: 860px) {
  img.profile-pic {
    height: 100px;
  }
}

@media screen and (max-width: 640px) {
  .App-header {
    height: 90px;
  }

  .App-header h1 {
    font-size: 30px;
  }

  .Board h4 {
    font-size: 14px;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}